div.footer {
    margin: 0 auto;
    margin-top: 62px;
    display: flex;
    justify-content: space-between;
    width: 77.08vw;
    padding: 16px 0;
    border-top: 1px solid #BFC4C9;
}

div.footer div.copyrights {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
}

div.footer div.socials {
    width: 168px;
    display: flex;
    gap: 24px;
    cursor: pointer;
}

div.footer div.socials img:first-child {
    width: 9.9px;
    height: 18px;
}

div.footer div.socials img:first-child + img {
    width: 18px;
    height: 18px;
}

div.footer div.socials img:first-child + img + img {
    width: 22px;
    height: 13.2px;
    margin-top: 5px;
}

div.footer div.socials img:first-child + img + img + img {
    width: 18px;
    height: 19px;
}

@media (max-width: 320px) {
    div.footer {
        flex-direction: column;
        gap: 19px;
        align-items: center;
        margin-top: 62px;
    }
    div.footer div.copyrights {
        text-align: center;
    }
    div.container div.mainBooks div.navigationList {
        margin-left: 5vw;
        width: 90vw;
    }
}

@media (min-width: 321px) and (max-width: 767px) {
    div.footer {
        flex-direction: column;
        gap: 19px;
        align-items: center;
        margin-top: 62px;
    }
    div.footer div.copyrights {
        text-align: center;
    }
}

@media (min-width: 1920px) and (max-width: 2560px) {
    div.footer {
        width: 1110px;
    }
}